import Swiper, { Pagination } from 'swiper';

export default () => {
  let windowWidth = window.innerWidth;
  let swiper;
  let inits = false;
  $(window).on('resize load', function () {
    windowWidth = window.innerWidth;

    if (windowWidth < 768) {
      if (!inits) {
        inits = true;
        Swiper.use([Pagination]);
        swiper = new Swiper('#services__list', {
          slidesPerView: 1,
          speed: 400,
          centeredSlides: true,
          spaceBetween: 30,
          pagination: {
            el: '.swiper-pagination',
            type: 'bullets',
            clickable: true,
          },
        });
      }
    } else {
      if (inits) {
        inits = false;
        swiper.destroy();
      }
    }
  });
};
