import Swiper, { Navigation } from 'swiper';

export default () => {
  Swiper.use([Navigation]);
  let swiper = new Swiper('.shares__block.bonus .swiper-container', {
    slidesPerView: 1,
    speed: 400,
    centeredSlides: true,
    spaceBetween: 60,
    autoHeight: true,
    navigation: {
      prevEl: '.shares__block.bonus .swiper-button-prev',
      nextEl: '.shares__block.bonus .swiper-button-next',
    },
  });
};
