export default () => {
  let offsetArray = $('.advantages__item');

  for (let i = 0; i < offsetArray.length; i++) {
    offsetArray[i].jsAnim = false;
    offsetArray[i].jsValue = parseInt(
      offsetArray[i].querySelector('b').textContent.replace(/[^\d]/g, '')
    );
    offsetArray[i].querySelector('b').textContent = 0;
  }

  $(window).on('scroll resize load', function () {
    let scroll = $(window).scrollTop() + window.innerHeight - 60;

    for (let i = 0; i < offsetArray.length; i++) {
      if (scroll > $(offsetArray[i]).offset().top && !offsetArray[i].jsAnim) {
        offsetArray[i].jsAnim = false;
        $(offsetArray[i])
          .prop('Counter', 0)
          .animate(
            {
              Counter: offsetArray[i].jsValue,
            },
            {
              duration: 800,
              easing: 'swing',
              step: function (now) {
                offsetArray[i].querySelector('b').textContent = Math.ceil(now)
                  .toString()
                  .replace(/\B(?=(?:\d{3})+(?!\d))/g, ' ');
              },
            }
          );
      }
    }
  });
};
