export default () => {
  $('.vip-text').on('click', function () {
    $(this).toggleClass('open');
    $(this).parent('.row').siblings('.vip-hidden').slideToggle(400);
  });
  $('.about__block.qa li button').on('click', function () {
    $(this).toggleClass('open');
    $(this).siblings('.qa_hidden').slideToggle(400);
  });
};
